/**
 * Create the loop delay with
 * the extra keyframes
 */

@-webkit-keyframes moveup {
    0%,
    60%,
    100% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
    }
    25% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
    }
}

@keyframes moveup {
    0%,
    60%,
    100% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
    }
    25% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(1em);
    }
}

@-webkit-keyframes movedown {
    0%,
    60%,
    100% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
    }
    25% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
    }
}

@keyframes movedown {
    0%,
    60%,
    100% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(0);
    }
    25% {
        transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
        -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
        -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
        -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg) translateZ(-1em);
    }
}


/**
   * Square layer styles
   */

.layer {
    display: block;
    position: absolute;
    /* position: fixed; */
    height: 3em;
    width: 3em;
    box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.2);
    transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg);
    -moz-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg);
    -ms-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg);
    -o-transform: rotateX(50deg) rotateY(0deg) rotateZ(45deg);
}

.layer:nth-of-type(1) {
    background: rgba(10, 10, 10, 0.5);
    margin-top: 2.5em;
    -webkit-animation: movedown 0.8s cubic-bezier(0.39, 0.575, 0.565, 1) 0.9se normal;
    animation: movedown 0.8s cubic-bezier(0.39, 0.575, 0.565, 1) 0.9se normal;
}

.layer:nth-of-type(1):before {
    content: '';
    position: absolute;
    width: 85%;
    height: 85%;
    background: rgba(0, 0, 200, 0.7);
}

.layer:nth-of-type(2) {
    background: rgba(10, 10, 10, 0.9);
    margin-top: 1.75em;
}

.layer:nth-of-type(3) {
    background: rgba(0, 0, 200, 0.9);
    -webkit-animation: moveup 0.8s cubic-bezier(0.39, 0.575, 0.565, 1)e normal;
    animation: moveup 0.8s cubic-bezier(0.39, 0.575, 0.565, 1)e normal;
}

