body {
    width: 100%;
    height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    /* background-color: rgba(255,255,255,1); */
}

.developer-banner {
    margin-top: 0em;
    width: 100%;
    box-shadow: rgb(25, 25, 25);
    box-sizing: border-box;
    height: 20em;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("https://res.cloudinary.com/dzflzvx3h/image/upload/v1597163514/darkbank/developer/office-932926_ngjcee.jpg");
    background-color: rgb(25, 25, 25);
    background-size: cover;
    color: rgba(255, 255, 255, 1);
    z-index: 1;
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
}

.developer-class {
    margin-left: 10%;
    width: 80%;
    margin-top: 2em;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    box-shadow: 0px 1px 10px rgba(10, 10, 10, 0.3), 0px -1px 1px 0.5px rgba(10, 10, 10, 0.3);
}

.job-image {
    width: 30%;
    box-sizing: border-box;
    border-radius: 0.1em;
    -webkit-border-radius: 0.1em;
    -moz-border-radius: 0.1em;
    -ms-border-radius: 0.1em;
    -o-border-radius: 0.1em;
    display: inline-block;
    margin-right: 2%;
    opacity: 0.9;
}

.job-details {
    width: 65%;
    box-sizing: border-box;
    border-radius: 0.1em;
    -webkit-border-radius: 0.1em;
    -moz-border-radius: 0.1em;
    -ms-border-radius: 0.1em;
    -o-border-radius: 0.1em;
    display: inline-block;
    margin-left: 0.5em;
    margin-bottom: 1em;
}

.role {
    /* margin-top: -5.5em; */
    font-size: 1.2em;
    color: #01060f;
    font-weight: bold;
}

.job-view {
    /*width: 20em;
    */
    width: 90%;
    height: 13em;
    margin-left: 5%;
    margin-top: 1em;
    border-radius: 0.2em;
    -webkit-border-radius: 0.2em;
    -moz-border-radius: 0.2em;
    -ms-border-radius: 0.2em;
    -o-border-radius: 0.2em;
}

.job-title {
    margin-top: 0.2em;
    color: #01060f;
    margin-left: 5%;
    font-weight: bold;
    font-size: 1.1em;
}

.job-info {
    font-size: 0.95em;
    margin-top: 0.5em;
    margin-left: 1em;
    color: #01060f;
    opacity: 0.9;
    /* font-size: 0.9em; */
}

.job-desc {
    color: #01060f;
    opacity: 0.9;
    margin-left: 5%;
}

.apply-box {
    margin-top: 0.5em;
}

.apply {
    font-weight: bold;
    height: 2.5em;
    width: 8em;
    font-size: 0.9em;
    color: rgba(255, 255, 255, 1);
    opacity: 0.9;
    background-color: #01060f;
    border: none;
    border-radius: 0.2em;
    -webkit-border-radius: 0.2em;
    -moz-border-radius: 0.2em;
    -ms-border-radius: 0.2em;
    -o-border-radius: 0.2em;
    transition: width 0.5s, height 0.5s;
    -webkit-transition: width 0.5s, height 0.5s;
    -moz-transition: width 0.5s, height 0.5s;
    -ms-transition: width 0.5s, height 0.5s;
    -o-transition: width 0.5s, height 0.5s;
}

.apply:hover {
    height: 2.2em;
    width: 6em;
    background-color: #01060f;
    opacity: 1;
}

@media screen and (max-width:1024px) {
    .job-info {
        margin-top: 0.5em;
        margin-left: 1em;
        color: #01060f;
        opacity: 0.8;
        font-size: 0.9em;
    }
    .developer-class {
        margin-left: 2%;
        width: 95%;
    }
    .job-details {
        margin-left: 0em;
    }
}

